.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.content-wrapper {
  position: relative;
  z-index: 1; /* Ensure content stays above the background image */
}


@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
.centering {
  height: 80vh;
}

.container {
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  justify-content: center;
  align-items: center;
  flex: 1; /* Allow container to grow or shrink as needed */
  padding:10px
}

.container-strava {
  display: flex;
  flex-direction: row;
  font-family: "Montserrat", sans-serif;
  justify-content: center;
  align-items: center;
  flex: 1; /* Allow container to grow or shrink as needed */
  padding: 10px
}


@media screen and (max-width: 500px) {
  .container-strava {
    flex-direction: column; /* Switch to column direction on smaller screens */
    height: auto; /* Allow height to adjust based on content */
    width: auto;
  }
}


.heading {
  font-size: 2.5rem;
  font-weight: bold;
}

.paragraph {
  font-size: 1.2rem;
  line-height: 1.5;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  width: auto
}

.regular{
  font-size: 1.2rem;
  line-height: 1.5;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}

.divider {
  width: 100%;
  height: 1px; /* Height of the divider line */
  background-color: #ccc; /* Color of the divider line */
  margin: 20px 0; /* Vertical margin to create space above and below the line */
}
