.paragraph::after {
  content: "|";
  position: relative;
  top: 0;
  right: 0;
  width: 0.3em;
  height: 1.2em;
  animation: blink-animation 1s infinite;
}

@keyframes blink-animation {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
.floating-image {
  animation: floatAnimation 3s ease-in-out infinite alternate;
}

@keyframes floatAnimation {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}
